<script lang="ts">
import _ from "lodash";

import { h, defineComponent, reactive, onMounted } from "vue";

import { Tabpane, px } from "../index";

import { SmartStore } from "@publishvue/smartstore";

const sm = new SmartStore();

var uid = 0;

// https://stackoverflow.com/questions/5306680/move-an-array-element-from-one-array-position-to-another?rq=1
function array_move(arr: any[], old_index: number, new_index: number) {
  if (new_index >= arr.length) {
    var k = new_index - arr.length + 1;
    while (k--) {
      arr.push(undefined);
    }
  }
  arr.splice(new_index, 0, arr.splice(old_index, 1)[0]);
  return arr; // for testing
}

abstract class DocNode {
  abstract renderMarkdown(): string;
  abstract renderHtml(): string;
  abstract serialize(): any;
  abstract fromProps(blob: any): DocNode;
  abstract deserialize(blob: any): DocNode;
  abstract body(): any;
  title() {
    return "Doc Node";
  }
  uid: number;
  parentEditor: MarkdownEditor;
  parentDoc: any;
  constructor(parentEditor: MarkdownEditor) {
    this.parentEditor = parentEditor;
    this.uid = uid++;
  }
  renderTitle() {
    return h(
      "div",
      {
        class: "titlebar",
        draggable: true,
        onDragstart: (ev: any) => {
          this.parentEditor.draggedUid = this.uid;
        },
        onDrop: (ev: any) => {
          ev.preventDefault();
          this.parentEditor.insertBefore(this.uid);
        },
        onDragover: (ev: any) => {
          ev.preventDefault();
        },
      },
      [
        h(
          "div",
          {
            class: "title",
          },
          [this.title()]
        ),
        h(
          "button",
          {
            class: "delete",
            onClick: () => {
              this.parentEditor.delete(this.uid);
            },
          },
          ["Delete"]
        ),
      ]
    );
  }
  renderNode() {
    return h(
      "div",
      {
        class: "docnode",
        uid: this.uid,
      },
      [this.renderTitle(), this.body()]
    );
  }
}

class TextNode extends DocNode {
  content: string;
  fontSize() {
    return 12;
  }
  title() {
    return "Text";
  }
  constructor(parentEditor: MarkdownEditor) {
    super(parentEditor);
    this.content = "";
  }
  fromProps(blob: any) {
    this.content = blob.content;
    return this;
  }
  serialize() {
    return {
      kind: "text",
      content: this.content,
    };
  }
  deserialize(blob: any) {
    this.content = blob.content;
    return this;
  }
  input(ev: any) {
    const text = ev.target.value;
    this.content = text;
    this.parentEditor.debounceUpdate();
  }
  body() {
    return h(
      "textarea",
      {
        style: {
          "font-size": px(this.fontSize()),
        },
        onInput: this.input.bind(this),
      },
      [this.content]
    );
  }
  renderMarkdown() {
    return this.content;
  }
  renderHtml() {
    return "<p>" + this.content + "</p>";
  }
}

class HeadingN extends TextNode {
  n: number;
  constructor(parentEditor: MarkdownEditor) {
    super(parentEditor);
    this.n = 1;
  }
  title() {
    return `Heading ${this.n}`;
  }
  fontSize() {
    return (5 - this.n) * 3 + 12;
  }
  fromProps(blob: any) {
    super.fromProps(blob);
    this.n = blob.n;
    return this;
  }
  serialize() {
    return {
      ...super.serialize(),
      ...{
        kind: "heading",
        n: this.n,
      },
    };
  }
  deserialize(blob: any) {
    super.deserialize(blob);
    this.n = blob.n;
    return this;
  }
  renderMarkdown() {
    return "#####".substring(0, this.n) + " " + this.content;
  }
  renderHtml() {
    return `<h${this.n}>${this.content}</h${this.n}>`;
  }
}

class MarkdownEditor {
  nodes: DocNode[];
  updateFunc: any;
  debounceUpdate: any;
  id: string = "markdowneditor";
  draggedUid: number;
  constructor(updateFunc?: any) {
    this.nodes = [];
    this.updateFunc = updateFunc;
    this.debounceUpdate = _.debounce(this.update.bind(this), 500);
    this.draggedUid = 0;
  }
  delete(uid: number) {
    this.nodes = this.nodes.filter((node) => node.uid !== uid);
    this.update();
  }
  insertBefore(uid: number) {
    const draggedIndex = this.nodes.findIndex(
      (node) => node.uid === this.draggedUid
    );
    const index = this.nodes.findIndex((node) => node.uid === uid);
    array_move(this.nodes, draggedIndex, index);
    this.update();
  }
  serialize() {
    const blob = {
      nodes: this.nodes.map((node) => node.serialize()),
    };
    return blob;
  }
  deserialize(blob: any) {
    this.nodes = blob.nodes.map((node: any) => {
      if (node.kind === "text") return new TextNode(this).deserialize(node);
      if (node.kind === "heading") return new HeadingN(this).deserialize(node);
    });
    return this;
  }
  update() {
    if (this.updateFunc) this.updateFunc();
  }
  addNode(node: DocNode) {
    this.nodes.push(node);
    this.update();
  }
  addText() {
    const node = new TextNode(this);
    this.addNode(node);
  }
  addHeadingN(n: number) {
    const node = new HeadingN(this);
    node.n = n;
    this.addNode(node);
  }
  renderNode() {
    return h(
      "div",
      {},
      this.nodes.map((node) => node.renderNode())
    );
  }
  renderMarkdown(): string {
    return this.nodes.map((node) => node.renderMarkdown()).join("\n");
  }
  renderHtml(): string {
    return this.nodes.map((node) => node.renderHtml()).join("\n");
  }
}

const react = reactive({
  updateKey: 0,
  markdown: "",
  html: "",
  width: 0,
  height: 0,
});

function calcSize() {
  react.width = window.innerWidth - 30;
  react.height = window.innerHeight - 105;
}

function updateMd() {
  react.updateKey++;
  react.markdown = md.renderMarkdown();
  react.html = md.renderHtml();
  const blob = md.serialize();
  sm.setItem(md.id, blob);
}

const md = new MarkdownEditor(updateMd);

function createHeadingNButton(n: number) {
  return h(
    "button",
    {
      onClick: () => {
        md.addHeadingN(n);
      },
    },
    [`+ Heading ${n}`]
  );
}

export default defineComponent({
  name: "MarkdownEditor",
  props: {
    id: {
      type: String,
      default: "markdowneditor",
    },
  },
  setup(props, context) {
    onMounted(async () => {
      md.id = props.id + "/markdowneditor";
      await sm.open();
      const storedDocJson = await sm.getItem(md.id, undefined);
      if (storedDocJson) md.deserialize(storedDocJson);
      calcSize();
      window.onresize = () => {
        calcSize();
      };
      updateMd();
    });

    return () => {
      const closeButton = h(
        "button",
        {
          onClick: () => {
            context.emit("closed", {});
          },
        },
        ["Close"]
      );

      const addTextButton = h(
        "button",
        {
          onClick: () => {
            md.addText();
          },
        },
        ["+ Text"]
      );

      const topControls = h(
        "div",
        {
          class: "topcontrols",
        },
        [
          closeButton,
          addTextButton,
          [1, 2, 3, 4, 5].map((i) => createHeadingNButton(i)),
        ]
      );

      const editorDiv = h(
        "div",
        {
          class: "editor",
          caption: "Editor",
        },
        react.updateKey === 0 ? "Loading editor ..." : md.renderNode()
      );

      const markdownPre = h("pre", {}, [react.markdown]);

      const markdownDiv = h(
        "div",
        {
          class: "markdown",
          caption: "Markdown",
        },
        [markdownPre]
      );

      const htmlDiv = h("div", {
        class: "html",
        caption: "Html",
        innerHTML: react.html,
      });

      const tabpane = h(
        Tabpane,
        {
          id: "editortabpane",
          width: react.width,
          height: react.height,
        },
        () => [editorDiv, markdownDiv, htmlDiv]
      );

      const vertContainer = h(
        "div",
        {
          class: "vertcont",
        },
        [topControls, tabpane]
      );
      return h(
        "div",
        {
          class: "markdowneditor",
        },
        [vertContainer]
      );
    };
  },
});
</script>

<style lang="scss">
@mixin centeredcol {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@mixin centeredrow {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.markdowneditor {
  textarea {
    padding: 5px;
  }
  @include centeredcol;
  .vertcont {
    @include centeredcol;
  }
  .topcontrols {
    padding: 5px;
    background-color: #eee;
    margin-bottom: 5px;
    width: calc(100% - 30px);
    text-align: center;
  }
  .docnode {
    padding: 2px;
    border: solid 1px;
    margin: 2px;
    margin-bottom: 6px;
    textarea {
      width: calc(100% - 12px);
      margin-bottom: -4px;
    }
    .titlebar {
      @include centeredrow;
      background-color: #eee;
      margin: 1px;
      margin-left: 0px;
      width: calc(100% - 1px);
    }
    .title {
      font-weight: bold;
      padding: 3px;
      background-color: #ffa;
      margin: 3px;
      width: 150px;
      text-align: center;
    }
    .delete {
      margin-left: 20px;
      background-color: #faa;
    }
  }
}
</style>
